body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1170px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

html {
  font-size: 10px;
}

body {
  font-family: 'Open Sans', sans-serif;
}

.hvr-underline-from-center::before {
  background: #000;
  height: 3px;
}

.hvr-fade:hover,
.hvr-fade:focus,
.hvr-fade:active {
  background-color: #000;
  color: #ffffff;
  cursor: pointer;
}

.hvr-fade:hover a,
.hvr-fade:focus a,
.hvr-fade:active a {
  color: #ffffff;
}

.main {
  background: url('../img/dot-grid.png');
}

/*Header*/

.main-header {
  padding-top: 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-header-logo {
  width: calc(25% - 30px);
  display: flex;
  align-items: center;
}

.main-header-descr {
  padding-left: 1rem;
}

.main-header-descr-name {
  font-size: 1.6rem;
  font-weight: 800;
}

.main-header-descr-prof {
  font-size: 1.2rem;
}

.main-header-nav {
  width: calc(50% - 30px);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.main-header-nav-item {
  font-size: 1.4rem;
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
  margin-left: 2rem;
  font-weight: 800;
  outline: none;
}

.main-header-nav-btn {
  border: 2px solid #2b2a2c;
  border-radius: 1.9rem;
  background: #fff;
  padding: 1rem 3rem;
  outline: none;
}

.main-header-nav-btn a {
  font-size: 1.4rem;
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 800;
}

/*Приветствие*/

.greatings-titles {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 10rem;
  font-size: 5rem;
}

.greatings-title {
  width: calc(50% - 30px);
  font-weight: 800;
}

.greatings-descr {
  width: calc(50% - 30px);
  font-weight: 800;
  text-align: right;
}

/*К портфолио*/

.down-link {
  padding-top: 10rem;
  padding-bottom: 6rem;
  text-align: center;
  font-size: 3rem;
  font-weight: 800;
}

.to-portfolio {
  display: block;
  padding-top: 1rem;
  opacity: 0.5;
  cursor: pointer;
}

.to-portfolio a {
  text-decoration: none;
  color: #000;
}

/*Карточки портфолио*/

.portfolio {
  background: url('../img/so-white.png');
}

.portfolio-title {
  text-align: center;
  font-size: 3rem;
  font-weight: 800;
  padding-top: 7rem;
}

.portfolio-cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 10rem 0 5rem 0;
}

.portfolio-card {
  width: calc(33.333333333333336% - 30px);
  border-radius: 1rem;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  margin-bottom: 5rem;
}

.portfolio-card-img img {
  width: 100%;
  border-radius: 10px 10px 0 0;
}

.portfolio-card-descr {
  padding: 1rem 2rem;
  background: #fff;
  border-radius: 0 0 10px 10px;
}

.portfolio-card-type {
  font-size: 1.2rem;
  color: #2b2a2c;
}

.portfolio-card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
}

.portfolio-card-name {
  font-size: 1.6rem;
  font-weight: 800;
}

.portfolio-card-view a {
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 800;
  color: #000;
}

.portfolio-card-view a:hover {
  opacity: 0.5;
}

/*Связь со мной*/

.feedback {
  background: url('../img/email-pattern.png');
}

.feedback-links {
  padding-top: 5rem;
  padding-bottom: 9rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
}

.feedback-work-social,
.feedback-social {
  width: calc(50% - 30px);
}

.feedback-title {
  text-align: center;
  font-size: 3rem;
  font-weight: 800;
  padding-top: 7rem;
}

.feedback-subtitle {
  text-align: center;
  font-size: 2rem;
  padding-top: 2rem;
}

.feedback-social-telegram {
  font-size: 1.6rem;
  padding-bottom: 2.5rem;
}

.feedback-social-telegram span {
  font-weight: 800;
}

.feedback-social-telegram span a {
  font-weight: normal;
  text-decoration: none;
  color: #000;
  text-decoration: underline;
}

.feedback-social-instagram {
  font-size: 1.6rem;
}

.feedback-social-instagram span {
  font-weight: 800;
}

.feedback-social-instagram span a {
  font-weight: normal;
  text-decoration: none;
  color: #000;
  text-decoration: underline;
}

.feedback-work-email {
  font-size: 1.6rem;
  padding-bottom: 2.5rem;
}

.feedback-work-email strong {
  font-weight: 800;
}

.feedback-work-linkedin {
  font-size: 1.6rem;
}

.feedback-work-linkedin span {
  font-weight: 800;
}

.feedback-work-linkedin span a {
  font-weight: normal;
  text-decoration: none;
  color: #000;
  text-decoration: underline;
}

/*Футер*/

.footer {
  background: #2b2a2c;
}

.footer-items {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  padding: 1rem 0;
}

.footer-left-side-item {
  width: calc(50% - 30px);
}

.footer-left-side-item a {
  text-decoration: none;
  color: #8d8d8e;
  font-size: 1.4rem;
  padding-right: 2rem;
}

.footer-left-side-item a:hover {
  color: #fff;
}

.footer-right-side {
  width: calc(50% - 30px);
  text-align: right;
}

.footer-right-side-btn {
  border: 1px solid #8d8d8e;
  border-radius: 1.9rem;
  background: transparent;
  padding: 1rem 3rem;
  outline: none;
}

.footer-right-side-btn:hover {
  border: 1px solid #fff;
  cursor: pointer;
}

.footer-right-side-btn:hover a {
  color: #fff;
}

.footer-right-side a {
  font-size: 1.4rem;
  color: #8d8d8e;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 800;
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .main-header-logo {
    width: calc(33.333333333333336% - 30px);
  }

  .main-header-nav {
    width: calc(69.16666666666667% - 30px);
  }

  .main-header-nav {
    justify-content: flex-end;
  }

  .portfolio-card {
    width: calc(50% - 30px);
  }

  .main-header-nav-btn {
    margin-left: 2rem;
  }

  .greatings-titles {
    font-size: 3.5rem;
  }

  .portfolio-card-name {
    font-size: 1.5rem;
  }

  .portfolio-card-view a {
    font-size: 1.3rem;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .main-header-nav {
    width: calc(100% - 30px);
  }

  .main-header-nav {
    justify-content: space-between;
  }

  .greatings-titles {
    justify-content: center;
  }

  .greatings-title {
    width: calc(100% - 30px);
  }

  .greatings-descr {
    width: calc(100% - 30px);
  }

  .portfolio-card {
    width: calc(66.66666666666667% - 30px);
  }

  .feedback-work-social,
  .feedback-social {
    width: calc(100% - 30px);
  }

  .main-header-logo {
    display: none;
  }

  .greatings-titles {
    font-size: 3rem;
  }

  .greatings-title {
    text-align: center;
    padding-bottom: 3rem;
  }

  .greatings-descr {
    text-align: center;
  }

  .feedback-social {
    padding-bottom: 2.5rem;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .main-header-logo {
    width: calc(50% - 30px);
  }

  .main-header-nav {
    justify-content: center;
  }

  .main-header-nav-items {
    width: calc(100% - 30px);
  }

  .main-header-nav-btn {
    width: calc(62.50000000000001% - 30px);
  }

  .portfolio-card {
    width: calc(91.66666666666667% - 30px);
  }

  .footer-items {
    justify-content: center;
  }

  .footer-left-side-item {
    width: calc(100% - 30px);
  }

  .footer-right-side {
    width: calc(100% - 30px);
  }

  .main-header-nav-btn {
    margin-top: 2rem;
  }

  .main-header-nav-item {
    text-align: center;
  }

  .down-link {
    font-size: 2rem;
  }

  .footer-left-side {
    text-align: center;
  }

  .footer-right-side {
    text-align: center;
    margin-top: 2rem;
  }

  .footer-left-side-item a {
    padding: 0 2rem;
  }
}